.RepoCard {
  background-color: #dcdee2;
  width: 520px;
  height: 212px;
  padding: 5px;
  border-radius: 4px;
  margin-right: 20px;
  margin-bottom: 20px;
}

.RepoCardInfo {
  width: 50%;
  padding-left: 20px;
  text-align: left;
}

.RepoCardTitle {
  background-color: gray;
  color: white;
  font-size: 28px;
  height: 50px;
  border-radius: 4px;
}

.RepoCardImage {
}

.DefaultRepoCardImage {
  -webkit-animation: DefaultRepoCardImageSpin infinite 20s linear;
          animation: DefaultRepoCardImageSpin infinite 20s linear;
  height: 144px;
}

@-webkit-keyframes DefaultRepoCardImageSpin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes DefaultRepoCardImageSpin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}



